<template>
  <div class="classifyPage">
    <div class="headerTop">
      <!--  头部  -->
      <buyHeader>
        <!--搜索-->
        <div class="buySearch" alt="search" slot="search">
          <span class="buyGlassIcon icon iconfont  buy-icon-sousuo"></span>
          <span>搜索商品, 共22389款好物</span>
        </div>
      </buyHeader>
    </div>
    <div type="scroll-tab-view" title="ScrollTab">
      <div slot="content">
        <div class="left-panel">
          <cube-scroll>
            <cube-tab-bar
              v-model="selectedLabel"
              :data="classList"
              @change="changeHandler"
            ></cube-tab-bar>
          </cube-scroll>
        </div>
        <div class="right-panel">
          <cube-scroll ref="scroll">
            <ul>
              <router-link
                tag="li"
                v-for="(item, index) in scrollData"
                :key="index"
              >
                <div class="imgCon">
                  <img
                    :src="item.class_img ? config.cdnbase + item.class_img : ''"
                    alt=""
                  />
                </div>
                <div class="classTitle">{{ item.name }}</div>
              </router-link>
            </ul>
          </cube-scroll>
        </div>
      </div>
    </div>
    <tabBarFooter></tabBarFooter>
  </div>
</template>

<script>
import tabBarFooter from "../components/home/tabBerFooter";
import { mapState } from "vuex";

export default {
  name: "classifyPage",
  data() {
    return {
      classList: [],
      genTabLabels: "",
      selectedLabel: "",
      scrollData: [],
      selectid: ""
    };
  },
  created() {
    // this.scrollData = DATA_MAP[this.selectedLabel]
    this.genTabLabels = Object.keys(this.classInfo).map(label => {
      return label;
    });
    this.selectedLabel = this.classInfo[0].name;
    this.changeHandler(this.classInfo[0].name);
    console.log(this.selectedLabel);
  },
  mounted() {
    this.getClassList();
  },
  methods: {
    changeHandler(label) {
      //获取二级分类
      this.classInfo.map(item => {
        if (label === item.name) {
          this.scrollData = item.subclass;
          this.selectid = item.id;
        }
      });
      this.$nextTick(() => {
        // reset better-scroll'postion
        this.$refs.scroll.scrollTo(0, 0);
        // you need to caculate scroll-content height when your dom has changed in nextTick
        this.$refs.scroll.refresh();
      });
    },
    getClassList() {
      //将左侧列表提炼出来
      this.classInfo.map(item => {
        this.classList.push({ label: item.name, id: item.id });
      });
    }
  },
  computed: {
    ...mapState(["siteInfo", "classInfo", "language", "config"]),
    //语言转换
    lang() {
      return function(src) {
        return this.language ? this.language[src] : "";
      };
    }
  },
  components: {
    tabBarFooter
  }
};
</script>

<style lang="stylus" scoped>
>>> .cube-scroll-list-wrapper
  color #FFFFFF

  .cube-tab-bar
    flex-wrap: wrap

    .cube-tab
      width: 100%
      flex-basis: unset
      height: 25px
      line-height: 25px
      font-size: 14px
      color: #333
      transition: all .3s ease-in
      position relative

      &.cube-tab_active
        color: #b4282d

        i
          position absolute
          left 0
          top 20%
          height: 60%;
          width 3px
          background #b4282d

.left-panel
  position: absolute
  top: 44px
  left: 0
  bottom: 0
  width: 80px

.right-panel
  position: absolute
  top: 44px
  left: 80px
  right: 0
  bottom: 0

  ul
    text-align left

    li
      width: 32%
      display: inline-block
      align-items: center
      margin-top 10px

      .imgCon
        width: 50px
        height 50px
        margin: 0 10px 0 20px

        img
          width: 50px
          height 50px
          border-radius: 3px

      .classTitle
        color: #333
        font-size 14px
        margin: 3px 0px
        text-align center

.footer
  position fixed
  bottom 0
  width 100%
  max-width 768px
  background #FFFFFF
  >>> .buyFooter
    border-top 1px solid #d9d9d9

    i
      font-size 17px

    .footerLable
      margin-top 5px
      font-size 10px


.buyHeader
  padding 8px 15px
  text-align left
  .buySearch
    padding 7px 20px
    background #ededed
    width 90%
    font-size 14px
    vertical-align middle
    border-radius 3px
    box-sizing border-box
    margin auto
    text-align center

    & > span
      display inline-block
      vertical-align middle
      color #666

    .buyGlassIcon
      margin-right 5px
      font-size 14px
      font-weight 600
      color #898989
</style>
